body {
background-color: #282c34;

}

.App {
text-align: center;
max-width: 800px;
margin: 0 auto;
padding: 20px;
}

h1 {
color: #61dafb;
margin-bottom: 30px;
}

.note-input {
width: 100%;
height: 400px;
margin-top: 20px;
padding: 10px;
font-size: 16px;
color: #61dafb;
background-color: #282c34;
border-radius: 5px;
border: 1px solid #61dafb;
resize: none; /* Запрещаем изменение размеров текстового поля */
}

.controls {
display: flex;
justify-content: space-between;
align-items: center; /* Выравниваем элементы по вертикали */
margin-top: 20px;
}

.save-button {
padding: 10px 20px;
font-size: 16px;
background-color: #61dafb;
border: none;
border-radius: 5px;
cursor: pointer;
transition: background-color 0.3s ease; /* Добавляем плавное изменение цвета при наведении */
}

.save-button:hover {
background-color: #2aa8d6;
}

.file-input {
font-size: 16px;
}

.file-input-label {
display: inline-block;
padding: 10px 20px;
font-size: 16px;
background-color: #61dafb;
border: none;
border-radius: 5px;
cursor: pointer;
transition: background-color 0.3s ease; /* Добавляем плавное изменение цвета при наведении */
}

.file-input-label:hover {
background-color: #2aa8d6;
}

.filename-input {
width: 100%;
height: 40px;
margin-top: 20px;
padding: 5px 10px;
font-size: 16px;
color: #61dafb;
background-color: #282c34;
border-radius: 5px;
border: 1px solid #61dafb;
box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
}

/* Добавляем стили для ошибки */
.error-message {
color: red;
margin-top: 10px;
}

.error-input {
border-color: red;
}


.file-input-container {
  position: relative;
  display: inline-block;
}

.file-input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.file-input-text {
  display: inline-block;
  padding: 6px 12px;
  background-color: #f0f0f0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #61dafb;
}

